import React from 'react';
import InputBase from '@material-ui/core/InputBase';
import { InputProps, makeStyles } from '@material-ui/core';
import { createStyles, Theme } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import SearchIcon from '@material-ui/icons/Search';

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		root: {
			border: '1px solid #B8BCCF',
			fontSize: '15px',
			lineHeight: 1,
			borderRadius: '12px',
			padding: '10px 40px 10px 20px',
			fontWeight: 300,
			width: '100%'
		},
		input: {
			padding: 0
		},
		gridContainer: {
			position: 'relative'
		},
		iconRoot: {
			position: 'absolute',
			top: '50%',
			transform: 'translateY(-50%)',
			fontSize: '22px',
			width: '22px',
			height: '22px',
			right: '15px'
		},
		svgRoot: {
			position: 'absolute'
		}
	})
);

export function InputTextIcon(props: InputProps) {
	const classes = useStyles();

	return (
		<Grid container classes={{ container: classes.gridContainer }}>
			<InputBase classes={{ root: classes.root, input: classes.input }} {...props} />
			<div className={classes.iconRoot}>
				<SearchIcon fontSize={'inherit'} classes={{ root: classes.svgRoot }} />
			</div>
		</Grid>
	);
}
