import { SwitchComp } from 'shared/components/Elements/Fields';
import { Loader } from 'shared/components';
import isEmpty from 'lodash/isEmpty';
import { useConfiguration } from './useConfiguration';
import { useAppState } from '../../../../shared/state';
import { useTranslation } from 'react-i18next';

export function Configuration() {
	const { t } = useTranslation();
	const { loading, handleMemberSwitch, configurationResponse } = useConfiguration();
	const { isAdmin } = useAppState();
	return (
		<div className="pt-8">
			<h1 className="mb-1 text-lg font-semibold dark:text-white">{t('configuration')}</h1>
			{!loading && !isEmpty(configurationResponse) ? (
				<div className="b-configuration relative">
					<div className="b-configuration__item flex border-b border-kgrey-border-solid pb-4 pt-6">
						<div className="b-configuration__switch mr-5">
							<SwitchComp
								classNames="disabled:opacity-60"
								disabled={!isAdmin}
								checked={!configurationResponse.not_visible_for_new_clients}
								handleChange={handleMemberSwitch}
								name="not_visible_for_new_clients"
							/>
						</div>
						<div className="b-configuration__content">
							<div className="b-configuration__title mb-1 text-base dark:text-white">
								{t('make_my_business_public')}
							</div>
							<div className="b-configuration__title text-sm text-kgrey">
								{configurationResponse.not_visible_for_new_clients
									? t('business_not_visible')
									: t('business_is_visible')}
							</div>
						</div>
					</div>
					<div className="b-configuration__item flex border-b border-kgrey-border-solid pb-4 pt-6">
						<div className="b-configuration__switch mr-5">
							<SwitchComp
								classNames="disabled:opacity-60"
								disabled={!isAdmin}
								checked={configurationResponse.auto_accept_requests}
								handleChange={handleMemberSwitch}
								name="auto_accept_requests"
							/>
						</div>
						<div className="b-configuration__content">
							<div className="b-configuration__title mb-1 text-base dark:text-white">
								{t('auto_accept_new_clients')}
							</div>
							<div className="b-configuration__title text-sm text-kgrey">
								{t('any_client_will_be_auto_accepted')}
							</div>
						</div>
					</div>
				</div>
			) : (
				<Loader tiled withOverlay />
			)}
		</div>
	);
}
