import React, { useEffect, useState } from 'react';
import isEmpty from 'lodash/isEmpty';
import { useAxios } from '../../../../shared/hooks';
import { useAppState } from '../../../../shared/state';

type BConfiguration = {
	auto_accept_requests: boolean;
	not_visible_for_new_clients: boolean;
};
export function useConfiguration() {
	const { companyId } = useAppState();
	const { fetchData, loading, response } = useAxios({
		url: `/${companyId}/my/company-configuration/`
	});
	const { fetchData: changeConfigurationRequest } = useAxios({
		url: `/${companyId}/my/company-configuration/`,
		method: 'patch',
		dynamicValues: true,
		cb: {
			// eslint-disable-next-line @typescript-eslint/no-empty-function
			success: () => {},
			error: (error, additionalArgs) => {
				const args = additionalArgs[0];
				setConfigurationResponse(args.prevResponse);
			}
		}
	});

	const [configurationResponse, setConfigurationResponse] = useState<BConfiguration>(
		{} as BConfiguration
	);

	useEffect(() => {
		if (!isEmpty(response)) {
			setConfigurationResponse(response as unknown as BConfiguration);
		}
	}, [response]);

	useEffect(() => {
		if (companyId) {
			(async () => {
				await fetchData();
			})();
		}
	}, [companyId]);

	const handleMemberSwitch = async (e: React.ChangeEvent<HTMLInputElement>) => {
		const { name, checked } = e.target;
		const checkedType = name === 'not_visible_for_new_clients' ? !checked : checked;

		const prevResponse = { ...configurationResponse } as BConfiguration;
		setConfigurationResponse((prevState: BConfiguration) => {
			return { ...prevState, [name]: checkedType };
		});
		await changeConfigurationRequest({
			values: { is_business: true, is_client: true, [name]: checkedType },
			prevResponse
		});
	};

	return {
		loading,
		handleMemberSwitch,
		configurationResponse
	};
}
